import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			oid: '',
			payOrder: '',
			is_miniBind: uni.getStorageSync('is_miniBind'),
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		/**
		 * 支付信息
		 */
		payOrdera() {
			var that = this
			var oid = that.oid;







			var type = 'mini';

			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				oid: oid,
				type: type,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				oid: oid,
				type: type,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("payOrder", data, (res) => {
				if (res.data.code == 200) {
					that.payOrder = res.data.data;
				} else if (res.data.code == 406) {
					uni.showToast({
						title: "请先绑定微信",
						icon: 'none'
					})
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					})
				}
			})
		},
		








































































		

		querenchongzhi() {
			console.log('微信支付')
			var that = this
			uni.requestPayment({
				provider: 'wxpay',
				orderInfo: that.payOrder.wxParams, //微信、支付宝订单数据
				timeStamp: that.payOrder.wxParams.timeStamp,
				nonceStr: that.payOrder.wxParams.nonceStr,
				package: that.payOrder.wxParams.package,
				signType: that.payOrder.wxParams.signType,
				paySign: that.payOrder.wxParams.paySign,
				success: function(res) {
					rs.Toast('充值成功')
					setTimeout(function() {
						uni.switchTab({
							url: "/pages/tabar/order"
						})
					}, 1000);
				},
				fail: function(err) {
					console.log(err)
					rs.Toast("充值失败");
				}
		
			})
		},

		
		goPay() {
			var that = this
			var oid = that.oid;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				id: oid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				id: oid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("balancePay", data, (res) => {
				if (res.data.code == 200) {
					uni.showToast({
						title: '支付成功',
						duration: 2000,
						icon: "none"
					});
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/tabar/index'
						});
					}, 1000)
				}
				if (res.data.code == 400) {
					uni.showToast({
						title: res.data.msg,
						duration: 2000,
						icon: "none"
					});
				}
				if (res.data.code == 500) {
					uni.showToast({
						title: '网络错误',
						duration: 2000,
						icon: "none"
					});
				}
			})
		},
	},
	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad(options) {
		this.oid = options.oid
	},
	onShow() {
		this.payOrdera()
		app.aData.show = false;
	}
};
